<div class="img-cover"><a-modal v-model:visible="modalVisible" :title="title" cancelText="取消" cancelType="link" okText="确定" @onCancel="onCancel" :keyboard="false" @ok="onOk" :maskClosable="false"><vue-cropper ref="cropper" id="vue-cropper" :src="choseImg" :aspectRatio="aspectRatio" v-if="cropperShow"></vue-cropper></a-modal><div class="left-img" v-if="!oldImgSrc &amp;&amp; !afterImg " :style="{width: CWidth, height: CHeight}"><img src="../assets/img/exhibition/noimg.png"></div><img class="left-img" v-else :src="afterImg || oldImgSrc" :style="{width: CWidth, height: CHeight}"><div class="r-text"><div class="text">支持格式：<span>{{importType.join('、')}}</span><br>
最佳尺寸：<span>{{ sizeText }}</span><br>
图片大小：<span>{{fileSize}}M以内</span></div><a-upload accept=".jpg, .jpeg, .png" :showUploadList="false" :disabled="disabled" :beforeUpload="beforeUpload"><a-button type="dashed"><slot name="prefix"></slot>上传</a-button></a-upload></div></div>